.footer {
  background-color: $white-dark;
  &__title, &__nav {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__block-block {
    @include flex(space-between); }
  &__time {
    text-align: center;
    .icon {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10px;
      fill: $black;
      font-size: 21px; } }
  &__auth {
    @include lg {
      text-align: right; }
    a {
      text-decoration: underline; } }
  &__logo {
    @include lg {
      margin-bottom: 15px; } } }
