.form {
  $this: &;
  &__title {
    margin-bottom: 8px; }
  &__subttl {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__btn {
    @include notlast {
      margin-bottom: 25px;
      @include md {
        margin-bottom: 15px; } } }
  &__row {
    margin-left: -2px;
    margin-right: -2px;
    @include flex(flex-start, normal, row, wrap);
    margin-bottom: 10px; }
  &__col {
    width: 33.33333%;
    padding-left: 2px;
    padding-right: 2px;
    @include sm {
      width: 50%;
      margin-bottom: 4px; }
    @include xs {
      width: 100%; } }
  &__col-sm {
    @include sm {
      width: 100%; } }

  &--shadow {
    box-shadow: 0px 2px 14px 0px rgba(0, 0, 0, 0.1); }

  &--center {
    text-align: center;
    padding: 75px 25px 60px;
    background-color: $white;
    @include xl {
      padding: 55px 20px 50px; }
    @include lg {
      padding: 40px 20px; }
    @include md {
      padding: 30px 20px; }
    @include sm {
      padding: 20px 15px; }
    #{$this}__field {
      @include notlast {
        margin-bottom: 25px;
        @include md {
          margin-bottom: 15px; } } } } }

.input {
  input {
    background-color: $white;
    padding: 5px 15px;
    height: 44px;
    width: 100%;
    border: 1px solid $white;
    @include tr(border-color .3s);
    @include placeholder {
      color: $gray-light; }
    @include md {
      padding: 5px 10px; }
    @include sm {
      height: 40px;
      padding: 5px; }
    &:focus {
      border-color: $accent; } }
  &--border {
    input {
      padding: 5px 0;
      height: 40px;
      border: 0;
      border-bottom: 1px solid  $gray-light; } } }

.textarea {
  textarea {
    resize: none;
    border: 0;
    width: 100%;
    min-height: 90px;
    overflow: hidden;
    &:focus {
      border-color: $accent; }
    @include placeholder {
      color: $gray-light; } }
  &--border {
    textarea {
      border: 0;
      border-bottom: 1px solid  $gray-light; } } }
