.appartment {
  $this: &;
  position: relative;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 50% 50%;
  padding-top: 80%;
  margin-top: 85px;
  position: relative;
  z-index: 2;
  @include sm {
    margin-top: 60px; }
  @include xs {
    margin-top: 40px; }
  &__btn {
    position: absolute;
    z-index: 1;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    border: 7px solid  $accent;
    background-color: $white;
    @include sm {
      width: 30px;
      height: 30px;
      border-width: 2px;
      @include before {
        width: 15px;
        height: 2px; }
      @include after {
        height: 15px;
        width: 2px; } }
    &:hover {
      #{$this}__hint {
        visibility: visible;
        opacity: 1; } }
    &::before, &::after {
      content: '';
      background-color: $accent;
      display: block;
      @include vcenter; }
    &::before {
      width: 20px;
      height: 3px; }
    &::after {
      height: 20px;
      width: 3px; }
    &:nth-child(1) {
      z-index: 2;
      top: 27%;
      left: 12%;
      #{$this}__hint {
        bottom: 100%;
        left: calc(100% + 85px);
        @include before {
          right: calc(100% + 10px); }
        @include after {
          right: calc(100% + 10px + 95px); }
        @include sm {
          left: calc(100% + 10px);
          @include before {
            width: 22px;
            right: 100%; }
          @include after {
            right: calc(100% + 22px); } }
        @include xs {
          bottom: calc(100% + 10px);
          left: -12vw;
          @include before {
            display: none; }
          @include after {
            top: 100%;
            bottom: auto;
            left: calc(12vw + 13px);
            right: auto;
            height: 10px; } } } }
    &:nth-child(2) {
      top: 20%;
      left: 60%;
      #{$this}__hint {
        bottom: 100%;
        right: calc(100% + 85px);
        @include before {
          left: calc(100% + 10px); }
        @include after {
          left: calc(100% + 10px + 95px); }
        @include md {
          right: 0;
          bottom: auto;
          top: calc(100% + 60px);
          @include before {
            display: none; }
          @include after {
            bottom: 100%;
            top: auto;
            left: auto;
            right: 20px;
            height: 25%; } }
        @include sm {
          @include after {
            right: 13px; } }
        @include xs {
          top: calc(100% + 25px);
          right: -28vw;
          @include after {
            height: 20px;
            right: calc(28vw + 13px); } } } }
    &:nth-child(3) {
      top: 22%;
      right: 12%;
      #{$this}__hint {
        top: 100%;
        right: calc(100% + 85px);
        @include before {
          left: calc(100% + 10px);
          top: 70%; }
        @include after {
          top: auto;
          bottom: 30%;
          left: calc(100% + 10px + 95px); }
        @include sm {
          right: 0;
          top: calc(100% + 50px);
          @include before {
            display: none; }
          @include after {
            top: auto;
            bottom: 100%;
            left: auto;
            right: 13px;
            height: 20%; } }
        @include xs {
          right: -11vw;
          left: auto;
          @include after {
            right: calc(11vw + 13px); } } } } }
  &__hint {
    position: absolute;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    @include tr(opacity .3s, visibility .3s);
    @include before {
      border-top: 1px dashed $accent;
      // width: 95px
      width: 20%;
      position: absolute;
      top: 30%; }
    @include after {
      // height: 180px
      height: 65%;
      border-right: 1px dashed $accent;
      position: absolute;
      top: 30%; }
    @include xs {
 } } }      // display: none


.appartment-hint {
  display: block;
  background-color: $accent-light;
  padding: 15px;
  min-height: 295px;
  width: 470px;
  color: $white;
  text-align: left;
  @include md {
    min-height: 0;
    padding-bottom: 40px; }
  @include sm {
    width: 350px; }
  @include xs {
    width: 90vw; }
  &__title-sm {
    display: block;
    font-size: $fz-xs;
    color: #aac5b7;
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 10px; } }
  &__title {
    margin-bottom: 20px;
    display: block;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    display: block; } }
