.no-touch {
  .no-scroll {
    .header {
      right: 17px; } } }

.header {
  $this: &;
  background-color: $white;
  position: fixed;
  z-index: 15;
  color: $black;
  left: 0;
  right: 0;
  top: 0;
  @include ready {
    @include tr(transform .3s, top .3s); }
  &.is-short {
    top: -$header-top-height;
    @include md {
      top: -$header-top-height-md; }
    @include xs {
      top: -$header-top-height-xs; } }
  &__top {
    border-bottom: 1px solid $white-dark;
    height: $header-top-height;
    @include ready {
      @include tr(transform .3s, height .3s); }
    @include md {
      height: $header-top-height-md; }
    @include xs {
      height: $header-top-height-xs; } }
  &__bottom {
    height: $header-bottom-height;
    border-bottom: 1px solid $white-dark;
    @include ready {
      @include tr(transform .3s, height .3s); }
    @include md {
      height: $header-bottom-height-md; }
    #{$this}__inner {
      justify-content: space-between; } }
  &__inner {
    @include flex(flex-start, center);
    padding-top: 12px;
    padding-bottom: 12px;
    @include md {
      padding-top: 6px;
      padding-bottom: 6px; }
    @include sm {
      justify-content: space-between; } }
  &__contacts {
    flex-grow: 1;
    @include md {
      .header-contact:not(.header-contact--mob-visible) {
        display: none; } } }
  &__contacts-mob {
    display: none;
    @include md {
      display: block; } }
  &__lang {
    text-align: center;
    flex: 0 0 210px;
    @include md {
      flex: 0 0 80px; }
    @include sm {
      flex: 0 0 auto; } }
  &__btn {
    @include md {
      flex-grow: 1;
      padding-right: 15px; } }
  &__nav {
    @include md {
      position: fixed;
      z-index: 15;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      padding: 15px;
      background-color: $white;
      @include vertical;
      overflow: auto;
      transform: translate(0, -100%);
      @include ready {
        @include tr(transform .3s); }
      &.is-active {
        transform: translate(0, 0); } } }
  &__nav-in {
    position: relative;
    @include md {
      display: inline-block;
      vertical-align: middle;
      font-size: $fz; }
    @include sm {
      font-size: $fz-sm; } }
  &__burger {
    display: none;
    @include md {
      display: block; } }
  &__nav-close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 15px;
    display: none;
    @include md {
      display: block; } } }

.header-contacts {
  @include flex(flex-start, center);
  @include md {
    display: block; } }

.header-contact {
  // width: 33.33333%
  width: 25%;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 21px;
  font-weight: 500;
  text-align: center;
  @include notlast {
    border-right: 1px solid  $white-dark; }
  @include lg {
    font-size: $fz; }
  @include md {
    width: auto;
    font-size: $fz-sm;
    margin-right: 10px;
    margin-right: 10px;
    @include notlast {
      border-right: 0; }
    &.header-contact--mob-visible {
      padding-top: 0;
      padding-bottom: 0; } }
  @include xss {
    width: auto; }
  small {
    font-size: $fz-xs;
    color: red;
    display: block;
    @include xss {
      font-size: 11px; } } }

.lang {
  $this: &;
  position: relative;
  text-align: left;
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
  @include sm {
    padding-right: 0; }
  &:hover {
    #{$this}__list {
      display: block; } }
  &__title {
    display: block;
    position: relative;
    // padding-right: 15px
    @include after {
      @include icon-arrow(8, 8, 1, currentColor, -45);
      display: inline-block;
      margin-left: 10px;
      position: relative;
      top: -.2em; } }
  &__list {
    position: absolute;
    z-index: 1;
    left: 0;
    width: 100%;
    top: 100%;
    padding-top: 10px;
    background-color: $white;
    padding-left: 15px;
    padding-right: 15px;
    display: none;
    box-shadow: 0 0 5px rgba($black, .5); }
  &__link {
    display: block;
    margin-bottom: 10px;
    @include tr(color .3s);
    @include hover {
      color: $c-text; } } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  margin-top: -18px;
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px; }
  span:after {
    top: 18px; }
  span {
    position: relative;
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    // transition: all 0.3s
    backface-visibility: hidden;
    border-radius: 2px; }
