.hero {
  text-align: center;
  &--slider {
    text-align: left; } }

.hero-inner {
  color: $white;
  position: relative;
  overflow: hidden;
  // +cover
  @include lg {
    @include before {
      @include coverdiv;
      background-color: rgba($black, .2);
      z-index: 1; } }
  .container {
    width: 100%;
    flex-grow: 1;
    @include flex(center, normal, column); }
  &__bg {
    z-index: 0;
    @include coverdiv;
    @include cover;
    @include tr(transform .3s); }
  &__content {
    position: relative;
    z-index: 2;
    padding-top: 25px;
    padding-bottom: 25px;
    @include flex(center, normal, column);
    min-height: calc(100vh - #{$header-height});
    .is-ie & {
      min-height: 0;
      height: calc(100vh - #{$header-height}); }
    @include md {
      min-height: calc(100vh - #{$header-height-md});
      .is-ie & {
        min-height: 0;
        height: calc(100vh - #{$header-height-md}); } }
    @include xs {
      min-height: calc(100vh - #{$header-height-xs});
      .is-ie & {
        min-height: 0;
        height: calc(100vh - #{$header-height-xs}); } } }
  &__uptitle {
    margin-bottom: 10px;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 400; }
  &__uptitle-sm {
    font-size: $fz-xs;
    color: $c-text;
    font-weight: 700; }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; } }
  &__subttl-sm {
    font-size: $fz-xs;
    color: $c-text; }
  &__btns {
    margin-bottom: 40px;
    @include flex(flex-start, center);
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      display: block; } }
  &__btn {
    @include notlast {
      margin-right: 30px;
      @include sm {
        margin-right: 0;
        margin-bottom: 15px; } } } }
