.section {
  position: relative;
  .container {
    position: relative;
    z-index: 1; }
  &__title {
    margin-bottom: 15px;
    text-align: center;
    @include sm {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 50px;
    text-align: center;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 25px; }
    @include sm {
      margin-bottom: 15px;
      br {
        display: none; } } }
  &__img {
    font-size: 0; }
  &__s-text {
    @include sm {
      margin-bottom: 25px; } }

  &--gray {
    background-color: $gray; }
  &--simple {
    padding-top: 50px;
    padding-bottom: 50px;
    @include xl {
      padding-top: 40px;
      padding-bottom: 40px; }
    @include lg {
      padding-top: 30px;
      padding-bottom: 30px; } }
  &--img {
    .lazy {
      &.is-loaded {
        position: relative;
        right: -15%;
        @include lg {
          right: auto; } } } }
  &--bg {
    @include cover; } }

.section-img {
  position: absolute;
  font-size: 0;
  z-index: 0;
  @include sm {
    display: none; }
  &.is-bottom {
    bottom: 0; }
  &.is-right {
    right: 0; }
  &.is-right-100 {
    right: 10%; }
  &.is-right-50 {
    right: 5%; } }

.s-call {
  @include sm {
    .section-img {
      display: none; } }
  &--mt {
    margin-top: 125px;
    @include md {
      margin-top: 100px; }
    @include sm {
      // margin-top: 100px
      margin-top: 0; }
