.news {}

.news-item {
  $this: &;
  margin-bottom: 30px;
  height: calc(100% - 30px);
  box-shadow: 0px 0px 12.6px 1.4px rgba(0, 0, 1, 0.15);
  @include flex(flex-start, normal, column);
  @include sm {
    height: auto;
    margin-bottom: 15px; }
  &__img {
    @include cover;
    padding-top: 49%; }
  &__meta {
    padding-bottom: 20px;
    @include flex(space-between, center);
    @include md {
      padding-bottom: 10px; } }
  &__meta-title {
    font-weight: 700;
    font-size: $fz-xs;
    text-transform: uppercase; }
  &__content {
    padding: 20px 25px 55px;
    flex-grow: 1;
    @include flex(flex-start, normal, column);
    @include xl {
      padding: 20px 20px 40px; }
    @include lg {
      padding: 20px; }
    @include sm {
      padding: 15px; } }
  &__title {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 25px;
    flex-grow: 1;
    @include md {
      margin-bottom: 15px; } }

  &--sm {
    #{$this}__img {
      padding-top: 70%;
      @include sm {
        padding-top: 40%; }
      @include xs {
        padding-top: 50%; }
      @include xss {
        padding-top: 60%; } }
    #{$this}__content {
      padding: 20px;
      @include sm {
        padding: 15px; } } } }
