.hero-features {
  @include flex(); }

.hero-feature {
  padding-right: 15px;
  @include notlast {
    margin-right: 50px;
    @include md {
      margin-right: 30px; }
    @include sm {
      margin-right: 15px; }
    @include xs {
      margin-right: 0; } }
  &__icon {
    color: $yellow;
    margin-bottom: 12px;
    @include sm {
      margin-bottom: 8px;
      .icon {
        font-size: 40px; } }
    .icon {
      font-size: 52px; } }
  &__title {
    font-size: $fz-xs;
    font-weight: 400; } }

.features {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -2px;
  margin-right: -2px;
  &__item {
    width: 33.33333%;
    padding-left: 2px;
    padding-right: 2px;
    margin-bottom: 4px;
    @include sm {
      width: 50%; }
    @include xss {
      width: 100%; } } }


.feature {
  @include cover;
  background-color: $accent-light;
  text-align: center;
  padding: 50px;
  height: 100%;
  color: $white;
  @include xl {
    padding: 40px; }
  @include lg {
    padding: 30px; }
  @include md {
    padding: 20px; }
  @include sm {
    padding: 15px; }
  &.lazy {
    @include xss {
      padding: 0;
      padding-top: 50%; } }
  &__icon {
    color: $yellow;
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; }
    .icon {
      font-size: 52px; } }
  &__title {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 10px; } } }

.features-circle {
  position: relative;
  @include image('features/circle/bg.png');
  background-repeat: no-repeat;
  background-size: 43%;
  background-position: 50% 50%;
  padding-top: 58%;
  @include xl {
    background-size: 35%; }
  @include md {
    background-image: none;
    padding-top: 0;
    @include flex(flex-start, normal, row, wrap); }
  &__img {
    @include vcenter;
    padding-top: 35%;
    width: 35%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    @include xl {
      width: 27%;
      padding-top: 27%; }
    @include lg {
      width: 20%;
      padding-top: 20%; }
    @include md {
      display: none; } }
  &__btn {
    position: absolute;
    bottom: 8%;
    left: 50%;
    transform: translate(-50%, 0);
    @include lg {
      bottom: 5%; }
    @include md {
      bottom: 0; }
    @include md {
      position: static;
      width: 100%;
      transform: translate(0, 0);
      text-align: center; } } }

.feature-circle {
  @include flex(flex-start, center);
  position: absolute;
  @include md {
    position: static;
    transform: translate(0, 0) !important;
    margin-bottom: 15px;
    width: 50%; }
  @include xss {
    width: 100%; }
  &:nth-child(1) {
    top: 0;
    left: 50%;
    transform: translate(-50%, 0); }
  &:nth-child(2) {
    top: 14%;
    right: 8%; }
  &:nth-child(3) {
    top: 50%;
    transform: translate(0, -50%);
    right: 0%; }
  &:nth-child(4) {
    right: 8%;
    bottom: 14%; }
  &:nth-child(5) {
    bottom: 14%;
    left: 4%; }
  &:nth-child(6) {
    left: 0;
    top: 50%;
    transform: translate(0, -50%); }
  &:nth-child(7) {
    left: 4%;
    top: 14%; }
  &__img {
    margin-right: 10px;
    margin-left: 10px;
    @include lg {
      width: 80px; }
    @include md {
      width: 60px;
      order: 0;
      margin-left: 0;
      flex: 0 0 60px; } }
  &__text {
    @include md {
      order: 1;
      padding-right: 10px;
      br {
        display: none; } } } }

.feature-color {
  text-align: center;
  overflow: hidden;
  @include xs {
    margin-bottom: 15px; }
  &__img {
    position: relative;
    padding-top: 85%;
    @include image('features/color/bg.png');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; }
    img {
      @include vcenter;
      max-height: 100%;
      // +md
      //   // max-width: 60%
 } }      //   max-height: 100%
  &__title {
    margin-bottom: 10px;
    @include md {
      margin-bottom: 5px; } }
  &__text {
    @include xs {
      br {
        display: none; } } } }
