.photos {
  @extend .row;
  @include xs {
    margin-left: 0;
    margin-right: 0; } }

.photo {
  @include cover;
  padding-top: 25%;
  margin-bottom: 30px;
  display: block;
  position: relative;
  width: calc(50% - 30px);
  margin-left: 15px;
  margin-right: 15px;
  @include xs {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 15px;
    padding-top: 60%; }
  &--link {
    background-color: $accent;
    color: $white;
    .link-arrow {
      @include vcenter; }
    @include hover {
      .link-arrow__arr {
        width: 30px; } } } }
