.blog-article {
  @include sm {
    margin-bottom: 25px; }
  h2 {
    @extend .title;
    @extend .title--bold;
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  p, figure, blockquote, ul, ol {
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  img {
    width: 100%; }
  figcaption {
    font-style: italic;
    font-size: $fz-xs;
    color: $gray-light; }
  ul {
    list-style-type: disc;
    margin-left: 1.1em; }
  ol {
    list-style-type: decimal;
    margin-left: 1.1em; } }

