.service-item {
  $this: &;
  display: block;
  text-align: center;
  position: relative;
  border: 1px solid transparent;
  padding-top: 30px;
  padding-bottom: 100px;
  padding-left: 10px;
  padding-right: 10px;
  overflow: hidden;
  box-shadow: 0px 2px 11px 0px rgba(0, 0, 0, 0.12);
  height: calc(100% - 30px);
  margin-bottom: 30px;
  @include tr(border-color .3s, transform .3s);
  &:hover {
    border-color: $accent;
    @include scale(1.02);
    #{$this}__btns {
      opacity: 1;
      visibility: visible; }
    #{$this}__price {
      visibility: hidden;
      opacity: 0; } }
  @include xs {
    margin-bottom: 15px;
    height: auto; }
  &__title {
    margin-bottom: 10px;
    position: relative;
    z-index: 1; }
  &__subttl {
    position: relative;
    z-index: 1; }
  &__btn {
    margin-bottom: 20px;
    @include md {
      margin-bottom: 15px; } }
  &__link {
    color: $black; }
  &__img {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    position: relative;
    padding-top: 80%;
    img {
      @include vcenter;
      z-index: 0;
      max-height: 100%; } }
  &__price {
    position: absolute;
    bottom: -125px;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: $white-dark;
    border-radius: 50%;
    width: 235px;
    height: 235px;
    text-align: center;
    z-index: 0;
    padding-top: 10%;
    @include tr(opacity .3s, visibility .3s);
    .price {
      display: inline-block; }
    @include md {
      bottom: -150px; } }
  &__btns {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translate(-50%, 0);
    z-index: 1;
    @include tr(opacity .3s, visibility .3s);
    width: 100%;
    padding-left: 15px;
    padding-right: 15px; }

  &--link {
    @include flex(center, center);
    box-shadow: none;
    @include hover {
      border-color: transparent; }
    .service-link {
 } }      // flex: 0 0 265px

  &--hor {
    padding: 0;
    text-align: left;
    @include flex(flex-start, normal, row, wrap);
    &:hover {
      #{$this}__price {
        visibility: visible;
        opacity: 1; } }
    @include md {
      padding-bottom: 60px; }
    @include sm {
      padding-top: 15px; }
    #{$this}__img {
      width: percentage(3/12);
      padding-top: 22%;
      img {
        max-width: 90%; }
      @include sm {
        width: 100%;
        padding-top: 30%; }
      @include xs {
        padding-top: 40%; } }
    #{$this}__content {
      width: percentage(9/12);
      @include flex(center, normal, column);
      padding: 15px;
      @include sm {
        width: 100%; } }
    #{$this}__btns {
      position: static;
      visibility: visible;
      opacity: 1;
      transform: translate(0, 0);
      padding-left: 0;
      padding-right: 0;
      @include flex(flex-start, center);
      @include xs {
        display: block; } }
    #{$this}__btn {
      margin-bottom: 0;
      margin-right: 15px;
      @include xs {
        margin-right: 0;
        margin-bottom: 10px; } }
    #{$this}__link {
      @include flex(center, normal, column); }
    #{$this}__text {
      margin-bottom: 25px;
      @include md {
        margin-bottom: 15px; } }
    #{$this}__price {
      bottom: -140px;
      padding-top: 3%;
      right: 0;
      left: auto;
      transform: translate(0, 0);
      @include md {
        bottom: -160px; }
      @include xs {
        left: 50%;
        right: auto;
        transform: translate(-50%, 0);
        padding-top: 7%;
        bottom: -170px; } } } }



.service-link {
  @include flex(center, center);
  border-radius: 50%;
  background-color: $accent;
  width: 265px;
  height: 265px;
  color: $white;
  padding: 10px;
  @include hover {
    .link-arrow {
      &__arr {
        width: 30px; } } }
  @include md {
    width: 215px;
    height: 215px; } }
