.popup {
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  @include vertical;
  padding: 15px;
  visibility: hidden;
  opacity: 0;
  background-color: rgba($black, .9);
  overflow: auto;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  &.is-active {
    visibility: visible;
    opacity: 1; }
  &__inner {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    max-width: 450px;
    background-color: $white;
    position: relative;
    font-size: $fz;
    @include sm {
      font-size: $fz-sm; } }
  &__close {
    @include icon-close(20, 1, currentColor);
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1;
    @include xs {
      top: 10px;
      right: 10px; } } }
