.nav {
  li {
    display: inline-block;
    @include md {
      display: block;
      margin-bottom: 15px; }
    @include notlast {
      margin-right: 55px;
      @include xl {
        margin-right: 35px; }
      @include lg {
        margin-right: 10px; } } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $c-text; }
    &.is-active {
      color: $c-text; } }

  &--footer {
    li {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 10px;
        @include xl {
          margin-right: 0; }
        @include md {
          margin-bottom: 5px; } } }
    a {
      @include hover {
        color: $gray-light; } } } }
